import { FC, forwardRef, Ref } from 'react';
import styled from 'styled-components';

import { defaultThemeProp } from '@pega/cosmos-react-core';
import { getTableStyles } from '@pega/cosmos-react-core/lib/components/HTML/HTML';

const StyledTable = styled.table(props => getTableStyles(props.theme));

StyledTable.defaultProps = defaultThemeProp;

export const TableBody: FC<any> = forwardRef(function TableBody(props: any, ref: Ref<any>) {
  const { children, ...restProps } = props;
  return (
    <tbody {...restProps} ref={ref}>
      {children}
    </tbody>
  );
});

export const Table: FC<any> = forwardRef(function Table(props: any, ref: Ref<any>) {
  const { children, ...restProps } = props;
  return (
    <StyledTable {...restProps} ref={ref}>
      {children}
    </StyledTable>
  );
});

export const TableRow: FC<any> = forwardRef(function TableRow(props: any, ref: Ref<any>) {
  const { children, ...restProps } = props;
  return (
    <tr {...restProps} ref={ref}>
      {children}
    </tr>
  );
});

export const TableCell: FC<any> = forwardRef(function TableCell(props: any, ref: Ref<any>) {
  const { children, ...restProps } = props;
  return (
    <td {...restProps} ref={ref}>
      {children}
    </td>
  );
});
